import type { QueryDataType } from "@osdk/foundry.ontologies";

export function isNullableQueryDataType(input: QueryDataType): boolean {
  if (input.type === "null") {
    return true;
  }
  if (input.type === "union") {
    return input.unionTypes.some(t => isNullableQueryDataType(t));
  }
  return false;
}
